html {
    height: 100%;
    overflow-y: hidden;
}

body {
    background-color: #1b1c1d;
    height: 100%;
}

#root {
    margin: 0;
    height: 100%;
}

@font-face {
    font-family: envyCodeR;
    src: url(./fonts/envycode.ttf);
}

.App {
    margin: 0;
    height: 100%;
}

#welcome-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin: 0;
    padding: 0;
}

#orbit-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 50%;
    border: 1px solid red;
    border-radius: 50%;
}

.circle {
    /* margin: 0;
    position: absolute; */
    /* top: 50%; */
    /* transform: translate(0, -50%); */
    /* left: 50%; */
    position: absolute;
    height: 2em;
    width: 2em;
    background-color: #fff;
    border-radius: 50%;
    animation: orbit2 5s infinite linear;
    /* transform: rotate(-360deg);
    transition: transform 0.7s linear; */
}

.intro.container {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translate(0, -50%);
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-color: rgba(27, 28, 29, 0.9); */
    /* border: 0px;
    border-radius: 50%; */
}

.intro.container h1 {
    font-family: envyCodeR;
    font-size: 4.25vw;
    font-weight: bold;
    letter-spacing: 1.25vw;
    color: #6F8FAF;
}

.intro.container h2 {
    font-family: envyCodeR;
    font-size: 1.75vw;
    /* font-weight: lighter; */
    letter-spacing: 1.2vw;
    color: #6F8FAF;
    /* text-shadow: 0 0 1vw #F0FFFF; */
}



